.navbar {
    height          : 5rem;
    /*background-color : var(--background);*/
    background      : linear-gradient(to left, #FF5050, #FF9595);
    display         : flex;
    justify-content : space-between;
    align-items     : center;
    padding-inline  : 1rem;
}

.navbar .logo {
    height    : 4rem;
    width     : 4rem;
    transform : rotate(45deg);
}